#media-browser {
	display: none;
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.3);
	z-index: 10000;

	.modal-dialog {
		position: absolute;
		margin: 0;
		right: 2em;
		top: 2em;
		/*bottom: 2em;*/
		left: 2em;
		width: auto;
		height: auto;
	}

	.tab-content {
		padding: 0;
	}
}


#media-browser-explore {
	.detail-wrap {
		background-color: #f2f2f2;
	}

	.detail {
		margin: 1em;
		overflow: hidden;
		min-height: 120px;
	}

	.detail > div:not(:last-child) {
		float: left;
		margin-right: 2em;
	}

	.filename {
		max-width: 10em;
		word-break: break-all;
	}

	.toolbar {
		padding: 1em;
	}

	.grid {
		list-style: none;
		margin: 0;
		padding: 0.5em 0;
		overflow: hidden;

		.item {
			position: relative;
			float: left;
			width: 90px;
			height: 90px;
			margin: 1em;
			line-height: 90px;

			img {
				vertical-align: top;
				border: 1px solid #ccc;
			}

			.checkbox {
				display: inline-block;
				position: absolute;
				visibility: hidden;
				top: -0.5em;
				right: -0.5em;
				border: 1px solid #fff;
				outline: 2px solid #ccc;
				line-height: 1;
				color: #fff;
				margin: 0;
				background-color: #ccc;

				.fa {
					visibility: hidden;
					font-size: 1.2em;
					margin: 0.1em;
				}
			}

			&.checked {
				outline: 4px solid #ccc;

				& img {
					border-color: #fff;
				}

				& .checkbox {
					visibility: visible;

					.fa {
						visibility: visible;
					}
				}
			}

			&:hover {
				outline: 4px solid #39f;

				& img {
					border-color: #fff;
				}

				& .checkbox {
					visibility: visible;
					outline-color: #39f;
					background-color: #39f;
				}
			}
		}
	}
}
